<template>
    <div id="addressBook">
      {{ changeData }}
        <el-table
          :data="tableData"
          style="width: 100%;font-size: 16px;text-align: center;"
          :default-sort = "{prop: 'name', order: 'descending'}"
          >
          <el-table-column
            prop="name"
            label=""
            sortable
            width="100">
          </el-table-column>

          <el-table-column
            prop="departmentName"
            label=""
            width="100">
          </el-table-column>

          <el-table-column
            prop="positionName"
            label=""
            width="180">
          </el-table-column>

          <el-table-column
            prop="phone"
            label=""
            width="150">
          </el-table-column>

          <el-table-column
            prop="username"
            label=""
            width="250">
          </el-table-column>
          <el-table-column
            prop="address"
            label=""
            >
          </el-table-column>
        </el-table>
        <div id="pages">
          <el-pagination
            background
            layout="prev, pager, next"
            :total="pagination.total"
            :page-size="pagination.pageSize"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="pagination.pageNum"
            >
        </el-pagination>
        </div>
    </div>
</template>
  
<script>
    export default {
      data() {
        return {
          tableData: [],
          pagination: {
            pageNum: 1,
            total: 10,//
            pageSize: 10//
          }
        }
      },
      props:['faMsg'],
      computed: {
        changeData() {

          this.getData()
          console.log(this.faMsg)

          return this.faMsg
        }
      },
      mounted(){
        this.getData();
      },
      methods: {
        formatter(row, column) {
          return row.address;
        },
        getData(){
          
          let DataAll = []

          this.$request({
            url: '/users'
          }).then(res => {
            console.log(res)
            DataAll = res.data
            this.tableData=[]
            //
            let strlength = (this.pagination.pageNum - 1) * this.pagination.pageSize + 1;
            //
            let endlength = this.pagination.pageNum * this.pagination.pageSize;
            //
            //
            let resStrLength=0
            if(DataAll.length % this.pagination.pageSize == 0){
              resStrLength = (parseInt(DataAll.length / this.pagination.pageSize)-1) * this.pagination.pageSize + 1
            }else{
              resStrLength = parseInt(DataAll.length / this.pagination.pageSize) * this.pagination.pageSize + 1
            }
            //   
            if(strlength>resStrLength){
              strlength=resStrLength
            }
            //   
            if (endlength > DataAll.length) {
              endlength = DataAll.length;
            }
            //
            for (let i = strlength - 1; i < endlength; i++) {
              this.tableData.push(DataAll[i]);
            }
            //
            this.pagination.total = DataAll.length;

          })

        },
        //
        handleSizeChange(val) {
          this.pagination.pageSize=val
          this.getData()
        },
        //
        handleCurrentChange(val) {
          this.pagination.pageNum=val
          this.getData()
        }
      },
      }
    
</script>

<style>
  #addressBook{
    width:90%;
    height: 700px;
    margin: 50px auto;
  }
  #pages{
    width: 400px;
    height: 30px;
    margin: 20px auto;
  }
</style>